<script>
import marked from "marked";
var rendererMD = new marked.Renderer();
marked.setOptions({
  renderer: rendererMD,
  gfm: true,
  tables: true,
  breaks: false,
  pedantic: false,
  sanitize: false,
  smartLists: true,
  smartypants: false
});
export default {
  data() {
    return {
      navBar: [],
      uid: "",
      input1: "",
      titleH2: "",
      time: "",
      currValue: ""
    };
  },
  computed: {
    compiledMarkdown: function() {
      return marked(this.currValue, { pedantic: true });
    }
  },
  created() {
    this.uid = this.$route.query.id;
    this.$http.get("/outdata/parentClassifyList").then(res => {
      this.navBar = res.data;
    });
    this.$http.get("/outdata/articlePage/" + this.uid).then(res => {
      console.log(res);
      this.titleH2 = res.data.title;
      this.time = res.data.createTime;
      this.currValue = res.data.content;
    });
  },
  methods: {
    elSearch() {},
    goBack() {
      this.$router.go(-1);
    }
  }
};
