<script>
export default {
  data() {
    return {
      input1: "",
      navBar: [],
      navItem: "",
      levelList: "",
      num: 0,
      currentPage: 1,
      show: true,
      showtotal: true,
      page: {
        pageNum: 1,
        pageSize: 10
      },
      total: 0,
      listNav: [],
      listMain: [],
      listId: [],
      uid: ""
    };
  },
  methods: {
    elSearch() {},
    getParamsId() {
      this.uid = this.$route.query.id;
      switch (this.uid) {
        case 1:
          this.navItem = "团委概况";
          document.title = `${this.navItem} - 共青团广西师范大学委员会`;
          this.handleUpData();
          break;
        case 2:
          this.navItem = "学术科技";
          document.title = `${this.navItem} - 共青团广西师范大学委员会`;
          this.handleUpData();
          break;
        case 3:
          this.navItem = "校园文化";
          document.title = `${this.navItem} - 共青团广西师范大学委员会`;
          this.handleUpData();
          break;
        case 4:
          this.navItem = "创新创业";
          document.title = `${this.navItem} - 共青团广西师范大学委员会`;
          this.handleUpData();
          break;
        case 5:
          this.navItem = "权益维护";
          document.title = `${this.navItem} - 共青团广西师范大学委员会`;
          this.handleUpData();
          break;
        case 6:
          this.navItem = "网上团校";
          document.title = `${this.navItem} - 共青团广西师范大学委员会`;
          this.handleUpData();
          break;
        case 7:
          this.navItem = "学生组织";
          document.title = `${this.navItem} - 共青团广西师范大学委员会`;
          this.handleUpData();
          break;
        case 8:
          this.navItem = "从严治团";
          document.title = `${this.navItem} - 共青团广西师范大学委员会`;
          this.handleUpData();
          break;
        case 9:
          this.navItem = "社会实践";
          this.handleUpData();
          break;
        case 10:
          this.navItem = "信息平台";
          document.title = `${this.navItem} - 共青团广西师范大学委员会`;
          this.handleUpData();
          break;
        case 11:
          this.navItem = "最新新闻";
          document.title = `${this.navItem} - 共青团广西师范大学委员会`;
          this.handleUpDataId(11);
          break;
        case 12:
          this.navItem = "最新公告";
          document.title = `${this.navItem} - 共青团广西师范大学委员会`;
          this.handleUpDataId(12);
          break;
        case 13:
          this.navItem = "学院动态";
          document.title = `${this.navItem} - 共青团广西师范大学委员会`;
          this.handleUpDataId(13);
          break;
        default:
          document.title = `共青团广西师范大学委员会`;
      }
    },
    tab(index, item) {
      this.num = index;
      this.handleUpDataId(item.id);
    },
    handleCurrentChange(val) {
      this.page.pageNum = val;
      this.handleUpDataId(this.uid);
    },
    handleUpDataId(id) {
      this.$http
        .get("/outdata/articleList/" + id, {
          params: {
            pageNum: this.page.pageNum,
            pageSize: this.page.pageSize
          }
        })
        .then(res => {
          this.listMain = res.data.list;
          if (res.data.totals == "null") {
            this.showtotal = false;
          }
          this.total = res.data.totals;
        });
    },
    handleUpData() {
      this.listNav = [];
      this.$http.get("/outdata/childClassifyList/" + this.uid).then(res => {
        this.listNav = res.data;
        // res.data.map(item => {
        //   this.listNav.push(item.name);
        //   this.listId.push(item.id);
        // });
        this.num = 0;
        if (res.data.length > 0) {
          this.$http
            .get("/outdata/articleList/" + res.data[0]["id"], {
              params: {
                pageNum: this.page.pageNum,
                pageSize: this.page.pageSize
              }
            })
            .then(res => {
              this.listMain = res.data.list;
              this.total = res.data.totals;
            });
        } else {
          this.listMain = [];
        }
        // this.listId.forEach(item => {
        //   console.log(item);

        // });
      });
    }
  },
  mounted() {
    this.uid = this.$route.query.id;
    this.$http.get("/outdata/parentClassifyList").then(res => {
      this.navBar = res.data;
    });
    this.getParamsId();
    this.handleUpData();
    this.handleUpDataId(this.uid);
  },
  watch: {
    $route: "getParamsId"
  },
  computed: {
    // key() {
    //     return this.$route.name !== undefined? this.$route.name +new Date(): this.$route +new Date()
    // }
  }
};
